/* =============================================== */
/* ================= Burger Icon ================= */
document.querySelector('.header__burger').addEventListener('click', function(e) {
	[].map.call(document.querySelectorAll('.header__burger > i'), function(el) {
		el.classList.toggle('icon-burger');
		el.classList.toggle('icon-close');
	});
});

UIkit.util.on('#mobileMenu', 'hide', function (e) {
	[].map.call(document.querySelectorAll('.header__burger > i'), function(el) {
		el.classList.toggle('icon-close');
		el.classList.toggle('icon-burger');
	});
});

window.onkeyup = function (event) {
	if (event.keyCode == 27) {
		if (document.querySelector('.header__burger > i').classList == 'icon-close' ) {
			document.querySelector('.header__burger > i').classList.toggle('icon-burger');
			document.querySelector('.header__burger > i').classList.toggle('icon-close');
		}
	}
}
