document.querySelectorAll('.catalog__infoBtn > .uk-button').forEach(el => {
	el.addEventListener('click', function() {
		var x = this;
		if (x.innerHTML === "категории") {
			x.innerHTML = "<i></i> закрыть";
		} else {
			x.innerHTML = "категории";
		}
	});
});

/* =============================================== */
/* =============== Product slider ================ */

UIkit.util.on('.poproduct', 'itemshown', function (element) {
    element.target.classList.add('uk-active-custom');
});

UIkit.util.on('.poproduct', 'beforeitemhide', function (element) {
    element.target.classList.remove('uk-active-custom');
});

