/* =============================================== */

/* =============================================== */
/* ================= Product +- ================== */

var min = 1;
var max = 10;

document.querySelectorAll('.plusminus > .plus').forEach(btn => {
	btn.addEventListener('click', function() {
		var counterInput = this.previousElementSibling;
		var nextValue = parseInt(counterInput.value, 10) + 1;
		if (nextValue <= max) {
			counterInput.value = nextValue;
		}
	});
});

document.querySelectorAll('.plusminus > .minus').forEach(btn => {
	btn.addEventListener('click', function() {
		var counterInput = this.nextElementSibling;
		var nextValue = parseInt(counterInput.value, 10) - 1;
		if (nextValue >= min) {
			counterInput.value = nextValue;
		}
	});
});
